// frontend/pages/payment/bkash.js
import { useState } from "react";
import api from "../../utils/api";

export default function ManualPaymentPage() {
  const [price, setPrice] = useState("");
  const [trxID, setTrxID] = useState("");
  const [message, setMessage] = useState("");

  const handlePaymentSubmit = async () => {
    try {
      const { data } = await api.post("/api/payment/verify", { trxID, price });
      setMessage(data.message);
    } catch (err) {
      console.error(err);
      setMessage("‚ùå Payment verification error");
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center">
      <h1 className="text-3xl font-bold mb-4">Trust Market bKash Payment</h1>

      <p className="mb-2">Personal bKash Number:</p>
      <p className="font-bold mb-4">01735702012</p>

      <input
        placeholder="Amount"
        value={price}
        onChange={(e) => setPrice(e.target.value)}
        className="border p-2 mb-2"
      />

      <input
        placeholder="trxID"
        value={trxID}
        onChange={(e) => setTrxID(e.target.value)}
        className="border p-2 mb-2"
      />

      <button
        onClick={handlePaymentSubmit}
        className="bg-pink-600 text-white px-6 py-2"
      >
        Verify Payment
      </button>

      {message && <p className="mt-4 font-bold">{message}</p>}
    </div>
  );
}
